
@font-face {
  font-family: 'iconfont';
  /* project id 316354 */
  src: url("//at.alicdn.com/t/font_vi9bowpaxp52vs4i.eot");
  src: url("//at.alicdn.com/t/font_vi9bowpaxp52vs4i.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_vi9bowpaxp52vs4i.woff") format("woff"), url("//at.alicdn.com/t/font_vi9bowpaxp52vs4i.ttf") format("truetype"), url("//at.alicdn.com/t/font_vi9bowpaxp52vs4i.svg#iconfont") format("svg");
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html, body {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
@media screen and (min-width: 769px) {
html, body {
    width: 460px;
    margin: 0 auto;
    font-size: 46px !important;
}
body {
    box-shadow: 0 0 30px gray;
}
}
.fade-enter-active {
  transition: all .3s ease;
}
.fade-leave-active {
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
.fade-enter, .fade-leave-active {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
  opacity: 0;
}

/*reaet*/
a {
  text-decoration: none;
}
a:hover, a:visited, a:link, a:active {
  text-decoration: none;
}
::-webkit-scrollbar {
  display: none;
}
.fadeIn-enter-active, .fadeIn-leave-active {
  transition: all .4s;
}
.fadeIn-enter, .fadeIn-leave-active {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
#app {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.content .middle {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    overflow-y: scroll;
}
.content .vheader {
    height: 2.266667rem;
    padding-bottom: 0.266667rem;
}
@media screen and (min-width: 769px) {
.middle {
    width: 460px;
}
}



